@import "tailwindcss";
@plugin "daisyui";

@theme {
    --color-brand-primary: #5c7f67;
    /* Earthy Green */
    --color-brand-secondary: #ecf0f1;
    /* Light Gray/White */
    --color-brand-accent: #d35400;
    /* Burnt Orange for CTA */

    --font-display: "Playfair Display", serif;
    --font-body: "Inter", sans-serif;

    --spacing-container: 1280px;
}

/* Custom Utilities if needed, but prefer standard utilities */
@utility container-wrapper {
    max-width: var(--spacing-container);
    margin-inline: auto;
    padding-inline: 1.5rem;
}